.approach {
  background-color: $color-coral;
  border-radius: 0 0 30px 30px;

  overflow: hidden;

  @include onLaptop {
    border-radius: 200px 0 0 200px;
    margin-top: -85px;
    margin-left: 29px;
  }

  @include onDesktop {
    margin-left: 121px;
  }

  &__content {
    padding-block-start: 72px;
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include onTablet {
      padding-block-start: 128px;
    }

    @include onLaptop {
      display: grid;
      grid-template-columns: 1fr minmax(486px, 644px) minmax(430px, 460px) 1fr;
      grid-template-areas: "margin text photo photo";
      padding-block-start: 204px;
    }
  }

  &__text-part {
    padding-inline: 20px;
    max-width: 362px;
    margin: 0 auto;

    @include onTablet {
      padding-inline: 41px;
    }

    @include onLaptop {
      grid-column: text;
      padding: 0;
      margin: 0;
    }
  }

  &__marker {
    margin-bottom: 16px;
    color: $second-main-text-color;
  }

  &__title {
    font-size: 27px;
    margin-bottom: 32px;
    color: $second-main-text-color;

    @include onTablet {
      font-size: 32px;
    }
  }

  &__work-approaches {
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include onTablet {
      gap: 64px;
    }
  }

  &__work-approach {
    display: flex;
    align-items: center;
    gap: 34px;
  }

  &__img-box {
    padding: 15px;
    background-color: $second-main-text-color;
    border-radius: 50%;

    @include onTablet {
      padding: 20px;
    }
  }


  &__description {
    color: $second-main-text-color;
    font-size: 13px;
    font-weight: 400;
    line-height: 150%;

    @include onTablet {
      font-size: 15px;
    }
  }

  &__img {
    display: block;
    width: 100%;
    height: 390px;
    object-fit: cover;
    border-radius: 30px;

    @include onLaptop {
      grid-column: photo;
      border-radius: 200px 0 0 0;
      height: 680px;
    }
  }
}