.header {
  background-color: $background-color-blue;
  border-radius: 0 0 30px 30px;
  overflow: hidden;

  @include onLaptop {
    border-radius: 0 0 0 200px;
  }

  &__content {
    color: $second-main-text-color;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include onLaptop {
      display: grid;
      grid-template-columns: 1fr minmax(486px, 644px) minmax(430px, 460px) 1fr;
      grid-template-areas: "margin title photo photo";
    }
  }

  &__strategic {
    padding-inline: 20px;

    @include onTablet {
      padding-inline: 41px;
    }

    @include onLaptop {
      padding-inline: 0;
      padding-top: 120px;
      grid-column: title;
    }
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-weight: 600;
    font-size: 52px;
    line-height: 120%;
    letter-spacing: -2px;
    margin-bottom: 32px;

    @include onTablet {
      font-size: 64px;
      line-height: 68px;
      letter-spacing: -2px;
      margin-bottom: 24px;
    }

    @include onLaptop {
      max-width: 490px;
    }
  }

  &__slogan {
    font-size: 18px;
    line-height: 150%;
    max-width: 362px;

    margin-bottom: 72px;

    @include onTablet {
      margin-bottom: 56px;
    }

    @include onLaptop {
      margin-bottom: 83px;
    }
  }

  &__slider {
    position: relative;
    color: #253757;

    @include onLaptop {
      grid-column: photo;
    }

    &-intro {
      position: absolute;
      z-index: 1;
      bottom: 24px;
      left: 24px;

      @include onTablet {
        left: 41px;
        bottom: 32px;
      }

      @include onLaptop {
        display: flex;
        gap: 24px;
        align-items: center;
        bottom: 48px;
        left: 78px;
      }

      @include onDesktop {
        left: 48px;
      }
    }

    &-image {
      display: block;
      width: 100%;
      height: 390px;
      object-fit: cover;
      transform: scaleX(-1);
      @include onLaptop {
        height: 680px;
      }
    }

    &-buttons {
      display: flex;
      gap: 16px;
      margin-bottom: 16px;
    }

    &-title {
      font-size: 13px;
      font-family: Poppins;
      font-weight: 700;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: #253757;

      margin-bottom: 8px;
    }

    &-subtitle {
      font-size: 14px;
      line-height: 140%;
      color: #253757;
    }

    &-button {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      background-color: $second-main-text-color;
      border-radius: 50%;
      width: 32px;
      height: 32px;

      @include hover(transform, scale(1.2));
    }
  }
}