.section-title {
  font-family: Poppins;
  color: $title-color;
  font-size: 32px;
  line-height: 150%;
  font-weight: 600;

  &--large {
    font-size: 44px;
    line-height: 120%;

    @include onTablet {
      font-size: 52px;
    }
  }

  &--line-height {
    line-height: 130%;
  }
}