.components {
  background-color: $second-main-text-color;
  margin-bottom: 120px;

  @include onTablet {
    background-image: url("../images/components/components-bg.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0;
  }

  @include onLaptop {
    background-size: contain;
  }

  &__content {
    padding: 72px 0;
    text-align: center;
    max-width: 754px;
    margin: 0 auto;

    @include onTablet {
      padding: 200px 0;
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__description {
    font-size: 22px;
    font-weight: 600;
    line-height: 150%;
    margin-bottom: 56px;

    @include onTablet {
      font-size: 24px;
      margin-bottom: 64px;
    }
  }

  &__button {
    margin: 0 auto;
  }
}