.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px;
  background-size: cover;
  background-position: center;

  &--menu {
    background-image: url("../images/icons/menu.svg");

    @include hover(transform, scale(1.2));

    @include onLaptop {
      display: none;
    }
  }

  &--arrow {
    height: 18px;
    width: 18px;

    &-left {
      background-image: url("../images/icons/arrow-left.svg");
    }

    &-right {
      background-image: url("../images/icons/arrow-right.svg");
    }
  }

  &--close {
    background-image: url("../images/icons/close.svg");

    @include hover(transform, scale(1.2));
  }

  &--arrow-services {
    background-image: url("../images/icons/services-arrow.svg");
  }

  &--speaker {
    background-image: url("../images/icons/speaker.svg");
  }

  &--calendar {
    background-image: url("../images/icons/calendar.svg");
  }

  &--file-text {
    background-image: url("../images/icons/file-text.svg");
  }

  &--quotes-blue  {
    background-image: url("../images/icons/quotes-blue.svg");
  }

  &--quotes-yellow  {
    background-image: url("../images/icons/quotes-yellow.svg");
  }

  &--quotes-red  {
    background-image: url("../images/icons/quotes-red.svg");
  }

  &--social {
    transition: color $effectDuration;

    &:hover path {
      fill: #d2ebff;
    }
  }
}