.panel {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &--top {
    padding-block: 20px 48px;

    @include onTablet {
      padding-bottom: 56px;
    }

    @include onLaptop {
      padding-block: 48px 44px;
    }
  }

  &--footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 48px;

    @media (min-width: 760px) {
      gap: 24px;
    }

    @include onLaptop {
      flex-direction: row;
      align-items: center;
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    gap: 32px;
  }
}