.nav {
  display: flex;

  &--hidden {
    display: none;

    @include onLaptop {
      display: block;
    }
  }

  &--menu {
    display: block;
  }

  &__list {
    display: flex;
    gap: 48px;

    &--menu {
      flex-direction: column;
      gap: 24px;

      @include onTablet {
        gap: 40px;
      }
    }

    &--footer {
      flex-direction: column;
      gap: 24px;

      @media (min-width: 760px) {
        flex-direction: row;
        gap: 40px;
      }

      @include onLaptop {
        gap: 64px;
      }
    }
  }

  &__link {
    position: relative;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    color: $second-main-text-color;
    font-size: 13px;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;

    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: -8px;
      height: 2px;
      width: 100%;
      background-color: $color-coral;
      border-radius: 4px;

      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: left;
    }

    &--active::after {
      transform: scale(1);
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}