body, h1, h2, h3, h4, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 41px;
  }
}

@media (width >= 1024px) {
  .container {
    max-width: 1146px;
    margin-inline: auto;
    padding-inline: 54px;
  }
}

.page {
  color: #6c788b;
  scroll-behavior: smooth;
  background-color: #f2f9ff;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.header {
  background-color: #008aff;
  border-radius: 0 0 30px 30px;
  overflow: hidden;
}

@media (width >= 1024px) {
  .header {
    border-radius: 0 0 0 200px;
  }
}

.header__content {
  color: #fff;
}

.header__bottom {
  flex-direction: column;
  gap: 48px;
  display: flex;
}

@media (width >= 1024px) {
  .header__bottom {
    grid-template-columns: 1fr minmax(486px, 644px) minmax(430px, 460px) 1fr;
    grid-template-areas: "margin title photo photo";
    display: grid;
  }
}

