.about-us {
  &__content {
    display: flex;
    flex-direction: column;
    max-width: 754px;
    margin: 0 auto;
    gap: 24px;
    text-align: center;
    padding: 120px 0;

    @include onTablet {
      padding: 148px 0;
    }
  }

  &__description {
    font-size: 22px;
    font-weight: 600;
    line-height: 150%;

    @include onTablet {
      font-size: 24px;
    }
  }
}