.contacts {
  background-color: $background-color-blue;
  border-radius: 30px 30px 0 0;
  padding: 72 0 80px;

  @include onTablet {
    padding: 0 0 139px;
  }

  @include onLaptop {
    border-radius: 0px 200px 0 0;
  }

  &__content {
    @include onTablet {
      @include pageGrid;
    }
  }

  &__feedback {
    @include onTablet {
      grid-column: 1 / 5;
    }

    @include onLaptop {
      padding-top: 180px;
      grid-column: span 5;
    }
  }

  &__contact-us {
    padding-top: 80px;

    @include onTablet {
      grid-column: 1 / 5;
      padding-top: 120px;
    }

    @include onLaptop {
      padding-top: 180px;
      grid-column: 7 / 12;
    }

    @include onDesktop {
      grid-column: 8 / 12;
    }
  }

  &__title {
    color: #fff;
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }
  }
}