.button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 264px;
  height: 56px;
  background-color: $color-coral;
  color: $second-main-text-color;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  border-radius: 64px;

  transition: all $effectDuration;

  &--header {
    height: 50px;
    width: 280px;

    @include onTablet {
      width: 264px;
    }
  }

  &:hover {
    background-color: #ff7e6c;
  }
}