.services {
  padding: 120px 0;

  @include onTablet {
    padding: 148px 0;
  }

  @include onLaptop {
    padding: 278px 0 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 48px;
    max-width: 362px;
    margin: 0 auto;

    @include onTablet {
      margin: 0;
      max-width: 100%;
      @include pageGrid;
      row-gap: 56px;
    }
  }

  &__text-part {
    @include onTablet {
      max-width: 100%;
      grid-column: span 3;
    }

    @include onLaptop {
      grid-column: span 4;

      transform: translateY(-6%);
    }
  }

  &__marker {
    margin-bottom: 16px;
  }

  &__title {
    margin-bottom: 24px;
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      grid-column: 1 / -1;
      @include pageGrid;
    }

    @include onLaptop {
      grid-column: 7 / -1;
      grid-template-columns: repeat(6, 1fr);
    }
  }

  &__card {
    padding: 32px;
    background-color: $second-main-text-color;
    max-width: 100%;
    border-radius: 24px;
    box-shadow: 0px 9px 18px 0px rgba(37, 41, 49, 0.03);

    &--001,
    &--003 {
      @include onTablet {
        grid-column: 1 / 4;

        @include onLaptop {
        }
      }
    }

    &--002,
    &--004 {
      @include onTablet {
        grid-column: 4 / -1;
        transform: translateY(-50%);
      }
    }

    &-number {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 24px;
      width: 46px;
      height: 24px;
      border-radius: 23px;
      font-family: Poppins;
      font-size: 13px;
      font-weight: 600;

      &--001 {
        background-color: rgba(86, 204, 242, 0.1);
        color: #56CCF2;
      }

      &--002 {
        background-color: rgba(252, 201, 29, 0.1);
        color: #fcc91d;
      }

      &--003 {
        background-color: rgba(21, 216, 80, 0.1);
        color: #15d850;
      }

      &--004 {
        background-color: rgba(243, 99, 99, 0.1);
        color: #f36363;
      }
    }

    &-services {
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;
      margin-bottom: 32px;
    }

    &-link {
      display: flex;
      gap: 16px;
      align-items: center;
      transition: all $effectDuration;

      &:hover .icon--arrow-services {
        transform: scale(1.1) rotate(360deg);
        transition: 0.3s;
      }
    }

    &-arrow {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      width: 40px;
      height: 40px;

      &--001 {
        background-color: #56ccf2;
      }

      &--002 {
        background-color: #fcc91d;
      }

      &--003 {
        background-color: #15d850;
      }

      &--004 {
        background-color: #f36363;
      }
    }

    &-button-text {
      color: #334563;
      font-family: Poppins;
      font-size: 15px;
      font-weight: 600;
    }
  }
}