.testimonials {
  padding: 148px 0 120px;

  @include onTablet {
    padding: 148px 0;
  }

  @include onLaptop {
    padding: 151px 0 186px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 48px;

    @include onTablet {
      gap: 56px;
    }

    @include onLaptop {
      gap: 85px;
    }
  }

  &__marker {
    margin-bottom: 16px;
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      gap: 30px;
    }

    @include onLaptop {
      flex-direction: row;
      gap: 20px;
    }

    @media (min-width: 1280px) {
      gap: 102px;
    }
  }

  &__card {
    background-color: #fff;
    padding: 75px 39px 32px;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    @include hover(transform, scale(1.05));

    &-photo-container {
      position: relative;

      &::after,
      &::before {
        content: "";
        position: absolute;
        width: 109px;
        height: 44px;
        transform: rotate(-60deg);
        border-radius: 500px;
      }

      &::before {
        left: -40px;
        top: 0;
      }

      &::after {
        right: -40px;
        bottom: 72px;
      }

      @include onTablet {

        &::before,
        &::after {
          width: 139px;
          height: 56px;
        }

        &::before {
          left: -45px;
        }

        &::after {
          right: -45px;
        }
      }

      // @include onLaptop {

      //   &::before,
      //   &::after {
      //     width: 109px;
      //     height: 44px;
      //   }

      //   &::before {
      //     left: -40px;
      //   }

      //   &::after {
      //     right: -40px;
      //   }
      // }
    }

    &-photo-container--blue {
      &::before {
        background-color: #56CCF2;
      }

      &::after {
        background-color: #56CCF2;
      }
    }

    &-photo-container--yellow {
      &::before {
        background-color: #fcc304;
      }

      &::after {
        background-color: #fcc304;
      }
    }

    &-photo-container--red {
      &::before {
        background-color: #f25656;
      }

      &::after {
        background-color: #f25656;
      }
    }

    &-photo {
      position: relative;
      height: 110px;
      width: 110px;
      border-radius: 50%;
      margin-bottom: 67px;
      z-index: 1;

      @include onTablet {
        height: 140px;
        width: 140px;
        margin-bottom: 79px;
      }

      // @include onLaptop {
      //   height: 110px;
      //   width: 110px;
      //   margin-bottom: 67px;
      // }
    }

    &-testimonial {
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;
      margin: 8px 0 16px;
      max-width: 362px;

      @include onTablet {
        margin: 16px 0 16px;
      }

      @include onLaptop {
        // margin: 8px 0 16px;
        min-height: 80px;
      }

      @media (min-width: 1078px) {
        min-height: 64px;
      }
    }

    &-name {
      position: relative;
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;
      margin-bottom: 4px;
      color: $title-color;
    }

    &-career {
      font-family: Poppins;
      color: #C0CDD7;
      font-size: 13px;
      font-weight: 700;
      letter-spacing: 3px;
      text-transform: uppercase;
    }
  }
}