.expertise {
  text-align: center;
  background-color: $second-main-text-color;
  border-radius: 30px;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 56px;
    padding-block: 72px;

    @include onTablet {
      gap: 64px;
      padding-block: 128px;
    }
  }

  &-cards {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 56px;

    @include onTablet {
      gap: 72px;
    }

    @include onLaptop {
      flex-direction: row;
    }
  }

  &-card {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 362px;

    @include onLaptop {
      max-width: 214px;
    }

    &__img {
      margin: 0 auto;
      width: 184px;
      height: 166px;
      border-radius: 24px;
      margin-bottom: 32px;

      @include hover(transform, scale(1.05));
    }

    &__title {
      color: $title-color;
      font-family: Poppins;
      font-size: 20px;
      font-weight: 600;
      line-height: 140%;
      margin-bottom: 16px;
    }
  }
}