.form {
  display: flex;
  flex-direction: column;
  gap: 40px;

  @include onTablet {
    gap: 48px;
  }

  &-field {
    box-sizing: border-box;
    max-height: 41px;
    font-family: Poppins, sans-serif;
    color: #fff;
    outline: none;
    padding-bottom: 17px;
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #fff;

    transition: all 0.1s;

    &:-webkit-autofill {
      transition: background-color 999999s;
      -webkit-text-fill-color: #fff;
    }

    &:hover {
      padding-bottom: 16px;
      border-bottom: 2px solid #fff;
    }

    &::placeholder {
      font-family: Poppins, sans-serif;
      color: #fff;
      font-size: 15px;
      font-weight: 600;
      opacity: 0.7;
    }

    &::selection {
      background-color: #ff9c8e;
    }

    &--textarea {
      resize: none;
      overflow: hidden;
    }
  }

  &-button {
    margin-top: 16px;
    border: none;

    @include onTablet {
      margin-top: 32px;
    }
  }
}