.contact-us {
  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include onTablet {
      gap: 48px;
    }
  }

  &__contact {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &--gap {
      gap: 16px;
    }
  }

  &__contact-info {
    color: #fff;
    font-size: 24px;
    font-weight: 600;
    line-height: 150%;

    transition: color $effectDuration;

    &:hover {
      color: #d2ebff;
    }
  }

  &__social-icons {
    display: flex;
    gap: 32px;
  }
}