.swiper {
  width: 100%;
  border-radius: 30px;
  height: 390px;


  @include onLaptop {
    height: 680px;
    border-radius: 200px 0 0 0;
  }
}

.swiper-wrapper {
  border-radius: 30px;
  transform: scaleX(-1);
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}